import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { addReward } from 'dump/data';
import FormFilterReward from './FormFilterReward';

const columns = [
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'className',
  },
  {
    title: 'Số QĐ',
    dataIndex: 'regNum',
    key: 'regNum',
  },
  {
    title: 'Ngày QĐ',
    dataIndex: 'decDay',
    key: 'decDay',
  },
  {
    title: 'Cấp khen thưởng',
    dataIndex: 'rewardLevel',
    key: 'rewardLevel',
  },
  {
    title: 'Hình thức',
    dataIndex: 'form',
    key: 'form',
  },
  {
    title: 'Loại khen thưởng',
    dataIndex: 'typeOfReward',
    key: 'typeOfReward',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      addReward.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#reward/updateForm/${record.code}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class AddRewardPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Nhập khen thưởng">
        <Row>
          <Col span={24}>
            <FormFilterReward />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/reward/addForm" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={addReward} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AddRewardPage />
  </Page>
);



