import React, { Component } from 'react';
import { Col, Form, Input, Button } from 'antd';
import './style.scss';

@Form.create({ name: 'formTeacher' })
class FormFilterReward extends Component {
  state = {};
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="inline" onSubmit={this.handleSearch} className="login-form">
        <Col span={8}>
          <Form.Item label="Mã SV">
            {getFieldDecorator('email', { initialValue: '' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item label="Họ tên">
            {getFieldDecorator('email', { initialValue: '' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item label="">
            <Button type="primary" htmlType="submit">
              Tìm kiếm
            </Button>
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormFilterReward;



